import Vue from 'vue'
import VueRouter from 'vue-router'
// import Reg from '@/views/Reg/Reg.vue'
// import Main from '@/views/Main/Main.vue'
// import Home from '../views/Menu/Home/Home.vue'
// import Userinfo from '../views/Menu/User/UserInfo.vue'
// import UserAvatar from '../views/Menu/User/UserAvatar.vue'
// import UserPwd from '../views/Menu/User/UserPwd.vue'
// import ArtCate from '../views/Menu/ArtCate/ArtCate.vue'
// import ArtList from '../views/Menu/ArtCate/ArtList.vue'
import store from '../store'
// import Login from '@/views/Login/Login.vue'
// const Login = () => import('@/views/Login/Login.vue')

Vue.use(VueRouter)

const routes = [
  { path: '/login', name: 'Login', component: () => import('@/views/Login/Login.vue') },
  { path: '/reg', name: 'Reg', component: () => import('@/views/Reg/Reg.vue') },
  {
    path: '/',
    name: 'Main',
    component: () => import('@/views/Main/Main.vue'),
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'Home',
        component: () => import('../views/Menu/Home/Home.vue')
      },
      {
        path: 'user-info',
        name: 'Userinfo',
        component: () => import('../views/Menu/User/UserInfo.vue')
      },
      {
        path: 'user-avatar',
        name: 'UserAvatar',
        component: () => import('../views/Menu/User/UserAvatar.vue')
      },
      {
        path: 'user-pwd',
        name: 'UserPwd',
        component: () => import('../views/Menu/User/UserPwd.vue')
      },
      {
        path: 'art-cate',
        name: 'ArtCate',
        component: () => import('../views/Menu/ArtCate/ArtCate.vue')
      },
      {
        path: 'art-list',
        name: 'ArtList',
        component: () => import('../views/Menu/ArtCate/ArtList.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 路由的全局前置导航守卫
// 参数1：到哪里去
// 参数2：从哪里来
// 参数3  是否放行
router.beforeEach((to, from, next) => {
  // 当用户访问/路径时，并且当前未登录，就要强行跳转到登录页/login
  const whiteList = ['/login', '/reg']
  if (!whiteList.includes(to.path) && !store.state.token) {
    Vue.prototype.$message.error('请登录一下！！！')
    next('/login')
  }
  next()
})

export default router
